import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  GoBack,
  Sec,
  SeeMore,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
  VideoWrapper,
  ResponsiveVideoOuter,
  ResponsiveVideoWrapper,
  MovieInfo2,
  ImagesTrigger,
  SocialHr,
  BackgroundCont,
} from '../styles/searchstyles';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import EmptyPosterBig from '../../img/empty_poster_b.jpg';
import EmptyActor from '../../img/empty_actor.jpg';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import CloseIcon from '@material-ui/icons/Close';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Grow from '@material-ui/core/Grow';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import ImdbIcon from '../../img/imdb_icon.png';
import TvdbIcon from '../../img/tvdb_icon.png';
import HomeIcon from '@material-ui/icons/Home';
import { ReactComponent as TmdbIcon } from '../../img/tmdb_icon.svg';
import FastAverageColor from 'fast-average-color';

function Detailsmovie(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [isLoaded3, setIsLoaded3] = useState(false);
  const [isLoaded4, setIsLoaded4] = useState(false);
  const [item, setItem] = useState('');
  const [credits, setCredits] = useState('');
  const [actorsCount, setActorsCount] = useState(18);
  const [fanarts, setFanarts] = useState('');
  const [videos, setVideos] = useState('');
  const [vid, setVid] = useState(2);
  const [art1, setArt1] = useState(6);
  const [art2, setArt2] = useState(6);
  const [art3, setArt3] = useState(6);
  const [art4, setArt4] = useState(6);
  const [art5, setArt5] = useState(6);
  const [art6, setArt6] = useState(6);
  const [art7, setArt7] = useState(6);
  const [art8, setArt8] = useState(6);
  const [galery, setGalery] = useState(false);
  const [modalImg, setModalImg] = useState(false);
  const [showImages, setShowImages] = useState(false);
  const [movie_idd, setMovieId] = useState('');
  const [averageColor, setAverageColor] = useState('');
  const api_key = '58666352553834b5820ef436680fb2e5';
  const url = props.match.params.id;
  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPosterBig} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActor} />;
      }
    }
  };
  const handleMoreVideos = () => {
    if (vid === 1000) setVid(2);
    else setVid(1000);
  };
  const handleMoreImg = (a) => {
    switch (a) {
      case 1:
        if (art1 === 1000) setArt1(6);
        else setArt1(1000);
        break;
      case 2:
        if (art2 === 1000) setArt2(6);
        else setArt2(1000);
        break;
      case 3:
        if (art3 === 1000) setArt3(6);
        else setArt3(1000);
        break;
      case 4:
        if (art4 === 1000) setArt4(6);
        else setArt4(1000);
        break;
      case 5:
        if (art5 === 1000) setArt5(6);
        else setArt5(1000);
        break;
      case 6:
        if (art6 === 1000) setArt6(6);
        else setArt6(1000);
        break;
      case 7:
        if (art7 === 1000) setArt7(6);
        else setArt7(1000);
        break;
      case 8:
        if (art8 === 1000) setArt8(6);
        else setArt8(1000);
        break;
      default:
        break;
    }
  };
  const handleMore = () => {
    if (actorsCount === 1000) setActorsCount(18);
    else setActorsCount(1000);
  };
  const handleOpen = (src) => {
    setModalImg(src);
    setGalery(true);
  };
  const handleClose = () => {
    setGalery(false);
  };
  const getAverageColor = (href) => {
    const fac = new FastAverageColor();
    let imageURL = href;

    let downloadedImg = new Image();
    downloadedImg.crossOrigin = 'Anonymous';
    downloadedImg.src = imageURL;
    fac
      .getColorAsync(downloadedImg)
      .then((color) => {
        setAverageColor(color.rgb);
      })
      .catch((e) => {
        console.log(e);
      });
  };
  useEffect(() => {
    const url_to_match = url;
    const regex = /(\d+)/;
    const movie_id = url_to_match.match(regex)[0];
    setMovieId(movie_id);
    fetch(
      `https://api.themoviedb.org/3/movie/${movie_id}?api_key=${api_key}&append_to_response=external_ids,images&include_image_language=en,pl,null&language=pl-PL`
    )
      .then((res) => res.json())
      .then(
        (result) => {
          result.media_type = 'movie';
          setItem(result);
          setIsLoaded(true);
          if (movie_id) {
            if (props.match.url !== encodeURI(`/movie/${movie_id}-${result.title.toLowerCase().split(' ').join('-')}`)) {
              props.history.replace(encodeURI(`/movie/${movie_id}-${result.title.toLowerCase().split(' ').join('-')}`));
            }
          }
          if (result.title && result.release_date) {
            document.title = `Film: ${result.title} (${result.release_date.slice(0, 4)})`;
          } else {
            document.title = 'InfCinema';
          }
          getAverageColor(`https://image.tmdb.org/t/p/w92${result.poster_path}`);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/movie/${movie_id}/credits?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setCredits(result);
          setIsLoaded2(true);
        },
        (error) => {
          setIsLoaded2(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/movie/${movie_id}/videos?api_key=${api_key}`)
      .then((res) => res.json())
      .then(
        (result) => {
          setVideos(result);
          setIsLoaded4(true);
        },
        (error) => {
          setIsLoaded4(true);
          setError(error);
        }
      );
    window.scrollTo(0, 0);
  }, []);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded || !isLoaded2) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    // console.log(item.images.backdrops[Math.floor(Math.random() * item.images.backdrops.length)].file_path);
    return (
      <>
        {/* <PhotoGallery disableAutoFocus={true} open={galery} onClose={handleClose}>
          <div style={{ position: 'relative', backgroundColor: 'rgb(250, 252, 255)' }}>
            <img src={modalImg} />
            <CloseButton onClick={handleClose}>
              <CloseIcon fontSize='large' />
            </CloseButton>
          </div>
        </PhotoGallery> */}
        <PhotoGallery maxWidth='xs' fullWidth={true} open={galery} onClose={handleClose}>
          <img alt='Modal' src={modalImg} style={{ maxWidth: '100%', maxHeight: '100%' }} />
          <CloseButton onClick={handleClose}>
            <CloseIcon fontSize='large' />
          </CloseButton>
          <OpenFull target='_blank' href={modalImg}>
            Otwórz w pełnej rozdzielczości
          </OpenFull>
        </PhotoGallery>
        <BackgroundCont
          container
          spacing={0}
          url={`https://image.tmdb.org/t/p/w1280/${item.images?.backdrops.length > 0 && item.images?.backdrops[0].file_path}`}
          colorr={averageColor.slice(4).slice(0, averageColor.slice(4).length - 1)}>
          <GoBack>
            <ArrowBackIcon fontSize='large' onClick={() => (props.history.length > 2 ? props.history.goBack() : props.history.replace(''))} />
          </GoBack>
          <PhotoContainer item xs={12} md={6} lg={4}>
            {havePhoto(item, 'big')}
            <TitleItem>{item.title}</TitleItem>
            <span
              style={{
                width: '100%',
                maxWidth: '450px',
                backgroundColor: 'white',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                paddingBottom: '5px',
              }}>
              {item.release_date && ` ( ${item.release_date.slice(0, 4)} )`}
            </span>
            {item.external_ids && (
              <SocialHr>
                {item.homepage && (
                  <a href={item.homepage} target='_blank' rel='noopener noreferrer'>
                    <HomeIcon style={{ margin: '5px', color: '#333' }} />
                  </a>
                )}
                {item.external_ids.instagram_id && (
                  <a href={`https://www.instagram.com/${item.external_ids.instagram_id}`} target='_blank' rel='noopener noreferrer'>
                    <InstagramIcon style={{ margin: '5px', color: '#E1306C' }} />
                  </a>
                )}
                {item.external_ids.facebook_id && (
                  <a href={`https://www.facebook.com/${item.external_ids.facebook_id}`} target='_blank' rel='noopener noreferrer'>
                    <FacebookIcon style={{ margin: '5px', color: '#4267B2' }} />
                  </a>
                )}
                {item.external_ids.twitter_id && (
                  <a href={`https://www.twitter.com/${item.external_ids.twitter_id}`} target='_blank' rel='noopener noreferrer'>
                    <TwitterIcon style={{ margin: '5px', color: '#1da1f2' }} />
                  </a>
                )}
                {item.id && (
                  <a href={`https://www.themoviedb.org/movie/${item.id}`} target='_blank' rel='noopener noreferrer'>
                    <TmdbIcon alt='imdb' src={ImdbIcon} height='20px' style={{ margin: '5px' }} />
                  </a>
                )}
                {item.external_ids.imdb_id && (
                  <a href={`https://www.imdb.com/title/${item.external_ids.imdb_id}`} target='_blank' rel='noopener noreferrer'>
                    <img alt='imdb' src={ImdbIcon} height='20px' style={{ margin: '5px' }} />
                  </a>
                )}
                {item.external_ids.tvdb_id && (
                  <a href={`https://www.thetvdb.com/dereferrer/series/${item.external_ids.tvdb_id}`} target='_blank' rel='noopener noreferrer'>
                    <img alt='imdb' src={TvdbIcon} height='20px' style={{ margin: '5px' }} />
                  </a>
                )}
              </SocialHr>
            )}
          </PhotoContainer>

          <InfoContainer item xs={12} md={6} lg={8} xl={8}>
            <MovieInfo style={{ marginBottom: '15px' }}>
              Opis: <br />
              <span>{item.overview ? item.overview : 'Brak polskiego opisu'}</span>
            </MovieInfo>
            {item.tagline && (
              <MovieInfo style={{ marginBottom: '30px' }}>
                <span className='slag' style={{ fontStyle: 'italic', color: 'gray' }}>
                  {item.tagline}
                </span>
              </MovieInfo>
            )}
            <div style={{ borderTop: '1px solid gray', width: '100%', marginBottom: '5px' }} />
            <div style={{ display: 'flex', flexWrap: 'wrap' }}>
              <MovieInfo>
                Typ: <span>{item.media_type ? item.media_type : 'Brak danych'}</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Premiera: <span>{item.release_date ? item.release_date : 'Brak danych'}</span>
              </MovieInfo>
              {credits.crew && (
                <>
                  <MovieInfo>
                    Reżyseria:{' '}
                    <span>
                      {credits.crew.filter((item) => item.job === 'Director').length > 0
                        ? credits.crew
                            .filter((item) => item.job === 'Director')
                            .map((i, index) => (
                              <span key={index}>
                                <Link to={encodeURI(`/person/${i.id}-${i.name.toLowerCase().split(' ').join('-')}`)}>{i.name}</Link>
                                {index === credits.crew.filter((item) => item.job === 'Director').length - 1 ? '' : ' , '}
                              </span>
                            ))
                        : 'Brak danych'}
                    </span>
                  </MovieInfo>
                  <MovieInfo>
                    Scenariusz:{' '}
                    <span>
                      {credits.crew.filter((item) => item.job === 'Writer').length > 0
                        ? credits.crew
                            .filter((item) => item.job === 'Writer')
                            .map((i, index) => (
                              <span key={index}>
                                <Link to={encodeURI(`/person/${i.id}-${i.name.toLowerCase().split(' ').join('-')}`)}>{i.name}</Link>
                                {index === credits.crew.filter((item) => item.job === 'Writer').length - 1 ? '' : ' , '}
                              </span>
                            ))
                        : 'Brak danych'}
                    </span>
                  </MovieInfo>
                </>
              )}
              <MovieInfo>
                Gatunek: <span>{item.genres?.length > 0 ? item.genres.map((i) => i.name).join(', ') : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                Status: <span>{item.status ? item.status : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                Czas trwania: <span>{item.runtime > 0 ? `${item.runtime} minut` : 'Brak danych'}</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Język: <span>{item.spoken_languages?.length > 0 ? item.spoken_languages.map((i) => i.name).join(', ') : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                {item.production_countries?.length > 1 ? 'Kraje: ' : 'Kraj: '}
                <span>{item.production_countries?.length > 0 ? item.production_countries.map((i) => i.name).join(', ') : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                {item.production_companies?.length > 1 ? 'Wytwórnie: ' : 'Wytwórnia: '}
                <span>{item.production_companies?.length > 0 ? item.production_companies.map((i) => i.name).join(', ') : 'Brak danych'}</span>
              </MovieInfo>
            </div>
            <div style={{ borderTop: '1px solid gray', width: '100%', marginBottom: '5px', marginTop: '5px' }} />
            {isLoaded4 && (
              <MovieInfo2 style={{ width: '100%' }}>
                {videos.results?.length > 0 && (
                  <div
                    style={{
                      display: 'flex',
                      flexDirection: 'column',
                      alignItems: 'flex-start',
                      width: '100%',
                    }}>
                    {videos.results.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 0 && (
                      <div style={{ margin: '20px 15px 10px 15px', fontSize: '30px' }}>Zwiatun:</div>
                    )}
                    {videos.results && (
                      <VideoWrapper>
                        {videos.results
                          .filter((item) => item.type === 'Trailer' && item.site === 'YouTube')
                          .slice(0, vid)
                          .map((video, index) => (
                            <ResponsiveVideoOuter min={(vid > 2).toString()} key={index}>
                              <ResponsiveVideoWrapper>
                                <iframe
                                  style={{ position: 'absolute', top: '0', left: '0', right: '0', bottom: '0', width: '100%', height: '100%' }}
                                  allowFullScreen='allowfullscreen'
                                  mozallowfullscreen='mozallowfullscreen'
                                  msallowfullscreen='msallowfullscreen'
                                  oallowfullscreen='oallowfullscreen'
                                  webkitallowfullscreen='webkitallowfullscreen'
                                  title={video.name}
                                  src={`https://www.youtube.com/embed/${video.key}`}></iframe>
                              </ResponsiveVideoWrapper>
                            </ResponsiveVideoOuter>
                          ))}
                      </VideoWrapper>
                    )}
                  </div>
                )}
                {videos.results?.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 2 && (
                  <Grid item xs={12} onClick={() => handleMoreVideos()}>
                    {vid > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
                  </Grid>
                )}
              </MovieInfo2>
            )}
          </InfoContainer>
        </BackgroundCont>
        <Sec>Obsada:</Sec>
        <ItemWrapper>
          {credits.cast?.slice(0, actorsCount).map((person, index) => {
            return (
              <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                <Item to={encodeURI(`/person/${person.id}-${person.name.toLowerCase().split(' ').join('-')}`)}>
                  {havePhoto(person)}
                  <div className='tit2'>
                    {person.name}
                    <br />
                    <span className='character'>{person.character}</span>
                  </div>
                </Item>
              </Grow>
            );
          })}
        </ItemWrapper>
        {credits.cast?.length > 18 && (
          <div onClick={handleMore}>{actorsCount > 20 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}</div>
        )}
        {fanarts.status !== 'error' && <Sec style={{ marginBottom: '30px' }}>Grafiki:</Sec>}
        {showImages ? (
          <>
            {item.images.backdrops && (
              <OutWrp container spacing={0}>
                <CenterTitle item xs={12}>
                  Tła
                </CenterTitle>
                {item.images.backdrops.slice(0, art1).map((im, index) => {
                  return (
                    <PosterWrapper item xs={6} md={6} lg={2} key={index}>
                      <Posters
                        onClick={() => handleOpen(`https://image.tmdb.org/t/p/original${im.file_path}`)}
                        src={`https://image.tmdb.org/t/p/w300${im.file_path}`}
                        alt={im.id}
                      />
                    </PosterWrapper>
                  );
                })}
                {item.images.backdrops.length > 6 && (
                  <Grid item xs={12} onClick={() => handleMoreImg(1)}>
                    {art1 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
                  </Grid>
                )}{' '}
              </OutWrp>
            )}
            {item.images.posters && (
              <OutWrp container spacing={0}>
                <CenterTitle item xs={12}>
                  Plakaty
                </CenterTitle>
                {item.images.posters.slice(0, art2).map((im, index) => {
                  return (
                    <PosterWrapper item xs={6} md={6} lg={2} key={index}>
                      <Posters
                        onClick={() => handleOpen(`https://image.tmdb.org/t/p/original${im.file_path}`)}
                        src={`https://image.tmdb.org/t/p/w300${im.file_path}`}
                        alt={im.id}
                      />
                    </PosterWrapper>
                  );
                })}
                {item.images.posters.length > 6 && (
                  <Grid item xs={12} onClick={() => handleMoreImg(2)}>
                    {art2 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
                  </Grid>
                )}
              </OutWrp>
            )}
          </>
        ) : (
          <ImagesTrigger onClick={() => setShowImages(true)}>
            Załaduj Grafiki <ExpandMoreIcon fontSize='large' />
          </ImagesTrigger>
        )}
      </>
    );
  }
}

export default Detailsmovie;
